const Systems = (props) => (
  <select size="20">
      for (var system in props.systems) {
        <option>{props.systems[system].name}</option>
      }
      <option>{props.systems._yxf8pjwlhx9.name}</option>
      <option>{props.systems._ggghpez789b.name}</option>
  </select>
)

Systems.getInitialProps = async function () {

  let data = await import('../src/systems.json')
  console.log(data);

  return {
    systems: data
  }
}


export default Systems



